document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});

function iniciarApp() {
    let calcular = document.querySelector('.boton--calcular');
    calcular.addEventListener('click', calculate);
    let limpiar = document.querySelector('.boton--limpiar');
    limpiar.addEventListener('click', clear);

}

function calculate() {
    const logro1 = document.querySelector('.formulario__imput--1');
    let valor1 = logro1.value;
    const logro2 = document.querySelector('.formulario__imput--2');
    let valor2 = logro2.value;
    const logro3 = document.querySelector('.formulario__imput--3');
    let valor3 = logro3.value;
    const logro4 = document.querySelector('.formulario__imput--4');
    let valor4 = logro4.value;
    const logro5 = document.querySelector('.formulario__imput--5');
    let valor5 = logro5.value;
    const logro6 = document.querySelector('.formulario__imput--6');
    let valor6 = logro6.value;
    const logro7 = document.querySelector('.formulario__imput--7');
    let valor7 = logro7.value;
    const logro8 = document.querySelector('.formulario__imput--8');
    let valor8 = logro8.value;
    const logro9 = document.querySelector('.formulario__imput--9');
    let valor9 = logro9.value;
    const logro10 = document.querySelector('.formulario__imput--10');
    let valor10 = logro10.value;
    const logro11 = document.querySelector('.formulario__imput--11');
    let valor11 = logro11.value;
    const logro12 = document.querySelector('.formulario__imput--12');
    let valor12 = logro12.value;
    const apuesta = document.querySelector('.formulario__imput--apuesta');
    let valorApuesta = apuesta.value;
    let resultado;
    let total = document.querySelector('.formulario__imput--total');
    let mensaje = document.querySelector('.mensaje__parrafo');
    let veces;
    let alertaLogro = document.querySelector('.mensaje__alerta--logro');
    let alertaApuesta = document.querySelector('.mensaje__alerta--apuesta');
    

    function repeat(resultado, valorApuesta) {
        resultado = valorApuesta * resultado;
        total.value = resultado.toFixed(2);
        veces = (resultado / valorApuesta).toFixed(2);
        if(veces > 0)  {
            mensaje.textContent = `Su apuesta paga ${veces} veces el valor del Monto Apostado`;
        }
        return;
    }

    if(!valor1) {
        alertaLogro.classList.remove('d-none');
        alertaLogro.classList.add('bg-alerta');
    } else {
        alertaLogro.classList.remove('bg-alerta');
        alertaLogro.classList.add('d-none');
    }

    if (!valorApuesta){
        alertaApuesta.classList.remove('d-none');
        alertaApuesta.classList.add('bg-alerta');
    } else {
        alertaApuesta.classList.remove('bg-alerta');
        alertaApuesta.classList.add('d-none');
    }


    if(valor1) {
        if (valor1 > 0) {
            valor1 = valor1 / 100 + 1 ;
        } else {
            valor1 = Math.abs (100 / valor1) + 1 ; 
        }
        resultado = valor1;   
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }     
    } 

    if(valor1 && valor2) {
        if (valor2 > 0) {
            valor2 = valor2 / 100 + 1 ;
        } else {
            valor2 = Math.abs (100 / valor2) + 1 ; 
        }
        resultado = valor1 * valor2;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 

    if(valor1 && valor2 && valor3) {
        if (valor3 > 0) {
            valor3 = valor3 / 100 + 1 ;
        } else {
            valor3 = Math.abs (100 / valor3) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 

    if(valor1 && valor2 && valor3 && valor4) {
        if (valor4 > 0) {
            valor4 = valor4 / 100 + 1 ;
        } else {
            valor4 = Math.abs (100 / valor4) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 
    if(valor1 && valor2 && valor3 && valor4 && valor5) {
        if (valor5 > 0) {
            valor5 = valor5 / 100 + 1 ;
        } else {
            valor5 = Math.abs (100 / valor5) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 
    if(valor1 && valor2 && valor3 && valor4 && valor5 && valor6) {
        if (valor6 > 0) {
            valor6 = valor6 / 100 + 1 ;
        } else {
            valor6 = Math.abs (100 / valor6) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5 * valor6;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 
    if(valor1 && valor2 && valor3 && valor4 && valor5 && valor6 && valor7) {
        if (valor7 > 0) {
            valor7 = valor7 / 100 + 1 ;
        } else {
            valor7 = Math.abs (100 / valor7) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5 * valor6 * valor7;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 
    if(valor1 && valor2 && valor3 && valor4 && valor5 && valor6 && valor7 && valor8) {
        if (valor8 > 0) {
            valor8 = valor8 / 100 + 1 ;
        } else {
            valor8 = Math.abs (100 / valor8) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5 * valor6 * valor7 * valor8;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 

    if(valor1 && valor2 && valor3 && valor4 && valor5 && valor6 && valor7 && valor8 && valor9) {
        if (valor9 > 0) {
            valor9 = valor9 / 100 + 1 ;
        } else {
            valor9 = Math.abs (100 / valor9) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5 * valor6 * valor7 * valor8 * valor9;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 

    if(valor1 && valor2 && valor3 && valor4 && valor5 && valor6 && valor7 && valor8 && valor9 && valor10) {
        if (valor10 > 0) {
            valor10 = valor10 / 100 + 1 ;
        } else {
            valor10 = Math.abs (100 / valor10) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5 * valor6 * valor7 * valor8 * valor9 * valor10;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 

    if(valor1 && valor2 && valor3 && valor4 && valor5 && valor6 && valor7 && valor8 && valor9 && valor10 && valor11) {
        if (valor11 > 0) {
            valor11 = valor11 / 100 + 1 ;
        } else {
            valor11 = Math.abs (100 / valor11) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5 * valor6 * valor7 * valor8 * valor9 * valor10 * valor11;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    } 

    if(valor1 && valor2 && valor3 && valor4 && valor5 && valor6 && valor7 && valor8 && valor9 && valor10 && valor11 && valor12) {
        if (valor12 > 0) {
            valor12 = valor12 / 100 + 1 ;
        } else {
            valor12 = Math.abs (100 / valor12) + 1 ; 
        }
        resultado = valor1 * valor2 * valor3 * valor4 * valor5 * valor6 * valor7 * valor8 * valor9 * valor10 * valor11 * valor12;
        if(valorApuesta) {
            repeat(resultado, valorApuesta);
        }  
    }  
    
}

function clear () {
    const campos = document.querySelectorAll('.formulario__imput');
    campos.forEach(campo => {
        campo.value = '';
    });
    mensaje = document.querySelector('.mensaje__parrafo');
    mensaje.textContent = '';
    
}



